.login button {
  border: 1px solid rgba(0, 0, 0, .5);
  padding: 5px;
  margin-top: 5px;
  font-size: 13px;
}

.login button:hover {
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .2);
  transition: box-shadow .3s;
}

.formSummaryError {
  border: 1px solid red;
  padding: 5px;
  color: red;
}